body {
  background-color: #f4f4f4;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  font-family: Arial, sans-serif;
  display: flex;
}

.form-container {
  background-color: #fff;
  border-radius: 8px;
  width: 300px;
  padding: 20px;
  box-shadow: 0 0 10px #0000001a;
}

label {
  margin-top: 10px;
  display: block;
}

input, textarea {
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 100%;
  margin-top: 5px;
  padding: 8px;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #28a745;
  border: none;
  border-radius: 4px;
  width: 100%;
  margin-top: 15px;
  padding: 10px;
}

button:hover {
  background-color: #218838;
}

.hidden {
  color: green;
  text-align: center;
  margin-top: 20px;
  display: none;
}
